import { VaultsAddModelData } from '@/types/vaults'
import { Block, TradeConfirmAmountInfo } from '@cetus/design'
import { CoinPairImage } from '@cetus/ui-kit'
import {
  Box,
  Button,
  HStack,
  Heading,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  Text,
  VStack
} from '@chakra-ui/react'
import { VaultsAddResult } from '../vaults-v2/detail/VaultsAddResult'

type VaultsAddConfirmModelProps = {
  data: VaultsAddModelData
  isOpen: boolean
  onClose: () => void
  onSubmitClick: () => void
  calculateLpLoading?: boolean
}

export default function VaultsAddConfirmModel(props: VaultsAddConfirmModelProps) {
  const { isOpen, onClose, data, onSubmitClick, calculateLpLoading } = props
  const { feeTier, displayTokenA, displayTokenB, displayAmountA, displayAmountB, totalAmountValue, sharePool, lpAmountLimit, lpDecimals } = data

  return (
    <Modal autoFocus={false} returnFocusOnClose={false} isOpen={isOpen} onClose={onClose} isCentered>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>
          <Heading fontWeight="500" fontSize="16px">
            Deposit
          </Heading>
        </ModalHeader>
        <ModalCloseButton />
        <ModalBody textAlign="center" p="0px">
          <VStack w="100%" gap="20px" p="16px" pb="0px" pos="relative">
            <HStack w="100%" justifyContent="space-between">
              <HStack>
                <CoinPairImage
                  coinAIconUrl={displayTokenA?.logo_url}
                  coinBIconUrl={displayTokenB?.logo_url}
                  imageStyle={{
                    w: '40px',
                    h: '40px'
                  }}
                  imgBoxStyle={{
                    w: '40px',
                    h: '40px'
                  }}
                />
                <Text fontSize="16px" color="text_caption">
                  {`${displayTokenA?.symbol} - ${displayTokenB?.symbol}`}
                </Text>
              </HStack>

              <Block w="unset" p="6px 8px" borderRadius="12px" ml="4px">
                <HStack>
                  <Text>Fee Tier</Text>
                  <Text color="primary">{feeTier}</Text>
                </HStack>
              </Block>
            </HStack>

            {/* 交易数量展示 */}
            <TradeConfirmAmountInfo
              coinA={{
                amount: displayAmountA,
                ...displayTokenA
              }}
              iconParams={{
                xlinkHref: '#icon-icon_add',
                svgFill: 'text_caption'
              }}
              coinB={{
                amount: displayAmountB,
                ...displayTokenB
              }}
            />

            {/* 展示结果 */}

            <VaultsAddResult
              amountLimit={lpAmountLimit}
              inputTotalValue={totalAmountValue}
              preCalculateLoading={false}
              calculateLpLoading={calculateLpLoading}
              lpRate={sharePool}
              lpDecimals={lpDecimals}
              poolName={`${displayTokenA?.symbol} - ${displayTokenB?.symbol}`}
            />
            <Box h="52px" />
            <Button
              pos="absolute"
              bottom="0px"
              mt="4px"
              w="100%"
              h="52px"
              borderRadius="12px"
              fontSize="16px"
              fontWeight="500"
              onClick={() => {
                onSubmitClick()
              }}
            >
              Deposit
            </Button>
          </VStack>
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}
