import { PositionSlider } from '@/components/position/details/RemoveBlock'
import useVaultsButtonStatus from '@/hooks/vault-v2/useVaultsButtonStatus'
import { useCalculateSlippageAmount } from '@/hooks/vault-v2/useVaultsHelper'
import useVaultsRemove from '@/hooks/vault-v2/useVaultsRemove'
import useVaultsActionStore from '@/store/vaults-v2/useVaultsAction'
import useVaultsPositionStore from '@/store/vaults-v2/useVaultsPosition'
import { Block, TradeInputGroup } from '@cetus/design'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { HTextLabelBox } from '@cetus/ui-kit'
import { formatCurrency, formatNumber, fromDecimalsAmountFix } from '@cetus/utils'
import { Box, Button, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useRef, useState } from 'react'
import { SwitchAssetTab } from './SwitchAssetTab'
type VaultsRemoveProps = {
  category: string
  isReverse: boolean
  vaultId: string
  currentActionTab: string
  lpTokenInfo: Token
  isModal?: boolean
  isAutoRefresh: boolean
}
export default function VaultsRemove(props: VaultsRemoveProps) {
  const { currentAccount, onWalletModal } = useAccountStore()
  const { fromToken, toToken, calculateResult } = useVaultsActionStore()
  console.log('🚀🚀🚀 ~ VaultsRemove.tsx:27 ~ VaultsRemove ~ calculateResult:', calculateResult)
  const { currentVaultPosition } = useVaultsPositionStore()
  const [assetAction, setAssetAction] = useState('both')
  const [isCheckedZAP, setIsCheckedZAP] = useState(false)
  const { category, isReverse, vaultId, currentActionTab, lpTokenInfo, isModal, isAutoRefresh } = props
  const currentPosition = useMemo(() => {
    return currentVaultPosition
  }, [currentVaultPosition])
  const {
    amountInputA,
    amountInputB,
    amountValueA,
    amountValueB,
    calculateAvailableLoading,
    availableAmountA,
    availableAmountB,
    zapNumGtError,
    zapNumLtError,
    handleAmountInputChange,
    removePercent,
    handlePercentInputChange,
    doRemoveAction,
    showOnlySideTips,
    preCalculateLoading,
    inputTotalValue,
    setIsSlider,
    resetInputAmount,
    isSlider,
    fixAmountA,
    reCalculateResult
  } = useVaultsRemove(vaultId, category, fromToken, toToken, isReverse, assetAction, isCheckedZAP, currentPosition)

  const { btnText, btnDisabled } = useVaultsButtonStatus(
    amountInputA,
    amountInputB,
    availableAmountA || '',
    availableAmountB || '',
    fromToken,
    toToken,
    assetAction,
    false,
    zapNumGtError,
    zapNumLtError,
    category,
    isCheckedZAP
  )
  console.log('🚀🚀🚀 ~ VaultsRemove.tsx:72 ~ VaultsRemove ~ btnDisabled:', btnDisabled, btnText)

  // 最小可接收
  const { amountLimit } = useCalculateSlippageAmount(0, calculateResult?.burn_ft_amount, false)

  const LPBurnAmount = useMemo(() => {
    return formatNumber(fromDecimalsAmountFix(amountLimit || '0', lpTokenInfo?.decimals), lpTokenInfo?.decimals)
  }, [amountLimit, lpTokenInfo])

  useEffect(() => {
    console.log('🚀🚀🚀 ~ VaultsRemove.tsx:80 ~ VaultsRemove ~ sCheckedZAP, assetAction:', isCheckedZAP, assetAction)
    resetInputAmount()
  }, [isCheckedZAP, assetAction])

  const showTokenALoading = useMemo(() => {
    if (preCalculateLoading) {
      if (isSlider) {
        return true
      }
      if (isReverse) {
        return fixAmountA
      }
      return !fixAmountA
    }
    return false
  }, [preCalculateLoading, isSlider])

  const showTokenBLoading = useMemo(() => {
    if (preCalculateLoading) {
      if (isSlider) {
        return true
      }
      if (isReverse) {
        return !fixAmountA
      }
      return fixAmountA
    }
    return false
  }, [preCalculateLoading, isSlider])

  const isMounted = useRef(false)
  useEffect(() => {
    isMounted.current = true
  }, [])

  // useEffect(() => {
  //   if (isMounted.current) {
  //     reCalculateResult()
  //   }
  // }, [isAutoRefresh])

  return (
    <VStack gap={isModal ? '8px' : '12px'} w="100%" align="flex-start">
      {/* <Block bg="bg_six" borderColor="border_secondary" borderRadius="12px" p={{ base: '16px 8px 8px', lg: '16px' }} w="100%"> */}
      <Block
        bg={isModal ? 'transparent' : 'bg_six'}
        borderColor={isModal ? 'transparent' : 'border_secondary'}
        borderRadius="12px"
        // p={{ base: '16px 8px 8px', lg: '16px' }}
        p={{ base: '16px 8px 8px', lg: isModal ? '0' : '16px' }}
        w="100%"
      >
        <SwitchAssetTab
          label="Remove Amounts"
          displayTokenA={fromToken}
          displayTokenB={toToken}
          onSelectAssetTab={assetAction => {
            setAssetAction(assetAction)
          }}
          isCheckedZAP={isCheckedZAP}
          setIsCheckedZAP={setIsCheckedZAP}
          assetAction={assetAction}
          setAssetAction={setAssetAction}
          currentActionTab={currentActionTab}
          category={category}
        />
        <Box w="100%" position="relative" mt={isModal ? '8px' : '16px'}>
          <TradeInputGroup
            borderRadius="12px"
            from={{
              wrapStyle: {
                borderRadius: '12px',
                h: 'unset',
                p: { base: '20px 8px', lg: '20px 16px' }
              },
              calculateAvailableLoading,
              hideSelf: assetAction !== 'both' && assetAction !== fromToken?.coin_type,
              balance: availableAmountA || '',
              value: amountInputA,
              amountValue: amountValueA,
              loading: showTokenALoading,
              onChange: (value: string, isClickMax?: boolean, isClickHalf?: boolean) => {
                if (currentAccount?.address && !calculateAvailableLoading) {
                  setIsSlider(false)
                  handleAmountInputChange(value, true, isClickMax, isClickHalf)
                }
              },
              needRemainBalance: false,
              selectable: false,
              placeholder: '0.0',
              token: fromToken,
              balanceLabel: 'Available'
            }}
            to={{
              wrapStyle: {
                borderRadius: '12px',
                h: 'unset',
                p: { base: '20px 8px', lg: '20px 16px' }
              },
              balanceLabel: 'Available',
              calculateAvailableLoading,
              needRemainBalance: false,
              hideSelf: assetAction !== 'both' && assetAction !== toToken?.coin_type,
              balance: availableAmountB || '',
              value: amountInputB,
              amountValue: amountValueB,
              loading: showTokenBLoading,
              onChange: (value: string, isClickMax?: boolean, isClickHalf?: boolean) => {
                if (currentAccount?.address && !calculateAvailableLoading) {
                  setIsSlider(false)
                  handleAmountInputChange(value, false, isClickMax, isClickHalf)
                }
              },
              selectable: false,
              placeholder: '0.0',
              token: toToken
            }}
            iconParams={{
              xlinkHref: '#icon-icon_add',
              svgFill: 'text_caption',
              iconCursor: 'default'
            }}
            iconHover={false}
          />
        </Box>
      </Block>
      {!isModal && (
        <Block bg="bg_six" borderColor="border_secondary" borderRadius="12px" p={{ base: '16px 8px', lg: '16px' }}>
          <Text color="primary_gray">Amount</Text>
          <PositionSlider
            percentage={removePercent}
            onChange={(value: string | number) => {
              if (!calculateAvailableLoading) {
                console.log('🚀🚀🚀 ~ VaultsRemove.tsx:213 ~ VaultsRemove ~ assetAction:', assetAction)
                console.log('🚀🚀🚀 ~ VaultsRemove.tsx:214 ~ VaultsRemove ~ fromToken.address:', fromToken.address)
                handlePercentInputChange(Number(value.toString().replace('%', '')), assetAction == 'both' ? true : assetAction == fromToken.coin_type)
              }
            }}
          />
        </Block>
      )}

      <Block bg="bg_six" borderColor="border_secondary" borderRadius="12px" p="0px">
        <VStack w="100%" gap="20px">
          <Button
            m="-1px"
            isLoading={preCalculateLoading}
            w="100%"
            borderRadius="12px"
            h="52px"
            fontSize="18px"
            fontWeight="500"
            disabled={btnDisabled || preCalculateLoading}
            onClick={() => {
              if (currentAccount) {
                doRemoveAction()
              } else {
                onWalletModal(true)
              }
            }}
          >
            {btnText || (isCheckedZAP ? 'Zap Out' : 'Withdraw')}
          </Button>

          {zapNumGtError && (
            <VStack w="100%" pl="16px" pr="16px" pb="20px" alignItems="start">
              <Text
                color="primary_red"
                fontSize="12px"
                textAlign="left"
                w="100%"
                bg="primary_red_opacity.10"
                p="12px"
                borderRadius="8px"
                lineHeight="20px"
              >
                Single-asset withdraw can't be higher than $10,000 at a time.
              </Text>
            </VStack>
          )}

          {/* 展示计算结果 */}
          {calculateResult && !zapNumGtError && (amountInputA || amountInputB) && (
            <VStack w="100%" pb="20px" pl="16px" pr="16px" gap="0px">
              {assetAction !== 'both' && showOnlySideTips && (
                <Text color="text_paragraph" width="100%" fontSize="12px" lineHeight="1.8" pb="20px">
                  {showOnlySideTips}
                </Text>
              )}
              <VStack w="100%" gap="20px">
                <HTextLabelBox
                  label="Total Withdraw"
                  labelStyle={{
                    fontSize: '14px'
                  }}
                  value={formatCurrency(inputTotalValue, 2)}
                  valueStyle={{
                    fontSize: '14px'
                  }}
                  skeletonStyle={{
                    valueH: '14px'
                  }}
                  isLoading={preCalculateLoading}
                />
                <HTextLabelBox
                  label="LP Burn Amount"
                  value={`${LPBurnAmount} ${`${fromToken?.symbol} - ${toToken?.symbol}`}`}
                  labelStyle={{
                    fontSize: '14px'
                  }}
                  valueStyle={{
                    fontSize: '14px'
                  }}
                  skeletonStyle={{
                    valueH: '14px'
                  }}
                  isLoading={preCalculateLoading}
                />
              </VStack>
            </VStack>
          )}
        </VStack>
      </Block>
    </VStack>
  )
}
