import { CetusTooltip } from '@cetus/design'
import { Token } from '@cetus/types'
import { formatNumber, formatPercentage, symbolDataDisplayProcessing } from '@cetus/utils'
import {
  HStack,
  Image,
  Skeleton,
  Slider,
  SliderFilledTrack,
  SliderMark,
  SliderMarkProps,
  SliderThumb,
  SliderTrack,
  Text,
  VStack
} from '@chakra-ui/react'
import { useMemo } from 'react'

type AvailableCapacityProps = {
  percentage: string | number
  onChange: (value: string | number) => void
  depositRatio: string | number
  hardCapUSD: string | number
  vaultTvl: string | number
  availableCapacityUSD: string | number
  quoteCoin?: Token
  maxCapNum?: string
  availableCapacityWithQuoteCoin?: string
}

// 剩余可用部分 百分比添加操作滑杆
export default function AvailableCapacity({
  percentage,
  onChange,
  quoteCoin,
  availableCapacityWithQuoteCoin,
  availableCapacityUSD
}: AvailableCapacityProps) {
  const loading = useMemo(() => {
    return !availableCapacityUSD
  }, [availableCapacityUSD])

  const labelStyles = {
    fontSize: 'sm',
    position: 'absolute',
    ml: '-5px',
    top: '50%',
    transform: 'translateY(-50%)',
    zIndex: '100',
    borderRadius: '50%'
  }

  const sliderMarkList = [0, 25, 50, 75, 100]

  return (
    <>
      {loading ? (
        <VStack width="100%" maxHeight="164px" justifyContent="space-between">
          <Skeleton width="100%" />
          <Skeleton width="100%" />
          <Skeleton width="100%" />
        </VStack>
      ) : (
        <VStack width="100%" alignItems="start" gap="14px">
          <Text color="text_paragraph" textAlign="left">
            Available Capacity
          </Text>
          <HStack w="100%" alignItems="center" justifyContent="space-between" mt="12px">
            <CetusTooltip
              tooltip={
                <HStack>
                  <Image key={quoteCoin?.coin_type} src={quoteCoin?.logo_url} w="20px" h="20px" />
                  <Text color="text_caption">{formatNumber(availableCapacityWithQuoteCoin, 2)}</Text>
                  <Text color="text_caption">{quoteCoin?.symbol}</Text>
                </HStack>
              }
            >
              <HStack w="100%" alignItems="center" justifyContent="space-between">
                <Text color="text_caption" height="14px" textAlign="left" fontSize="16px" textDecoration="underline dotted">
                  {symbolDataDisplayProcessing(availableCapacityUSD, '$')}
                </Text>
              </HStack>
            </CetusTooltip>
            <Slider
              aria-label="slider-ex-6"
              min={0}
              max={100}
              focusThumbOnChange={false}
              value={percentage == '--' ? 0 : Number(percentage)}
              onChange={value => onChange(value)}
              w="242px"
              isDisabled={Number(availableCapacityUSD) <= 0}
            >
              {sliderMarkList
                .filter(item => item !== Number(percentage))
                .map((item, index) => (
                  <SliderMark
                    key={index}
                    {...(labelStyles as SliderMarkProps)}
                    value={item}
                    w="8px"
                    h="8px"
                    bg={Number(percentage) >= item ? 'primary' : '#2b2e32'}
                    onClick={() => onChange(item)}
                  />
                ))}
              <SliderMark
                value={Number(percentage)}
                textAlign="center"
                bg="#1B2E3B"
                p="2px 4px"
                borderRadius="4px"
                fontSize="12px"
                color="primary"
                mt="-28px"
                ml="-15px"
              >
                {formatPercentage(percentage, 2)}
              </SliderMark>
              <SliderTrack bg="#2b2e32" height="2px" borderRadius="4px">
                <SliderFilledTrack height="2px" bg="primary" borderRadius="2px" />
              </SliderTrack>
              <SliderThumb
                p="1px"
                w="10px"
                h="10px"
                border="1px solid primary"
                position="relative"
                zIndex="100"
                borderRadius="50%"
                _before={{
                  content: '""',
                  w: '6px',
                  h: '6px',
                  borderRadius: '50%',
                  bg: [0, 25, 50, 75, 100].includes(Number(percentage)) ? 'primary' : 'bg_secondary',
                  border: '1px solid #2b2e32'
                }}
              />
            </Slider>
          </HStack>
          {!loading && availableCapacityUSD == 0 ? (
            <VStack w="100%" alignItems="start">
              <Text
                color="primary_yellow"
                fontSize="12px"
                textAlign="left"
                w="100%"
                bg="primary_yellow_opacity.10"
                p="12px"
                borderRadius="8px"
                lineHeight="20px"
              >
                The vault has reached its current capacity. New deposits are not accepted at this time.
              </Text>
            </VStack>
          ) : null}
        </VStack>
      )}
    </>
  )
}
